body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  background-image: url("/static/media/Keda Tech Assessment BG.852800a4.svg");
  background-repeat: no-repeat;
  background-size: contain;
  margin-top: 60px;
  padding-top: 1rem;
}

.page {
  min-height: 90vh;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width: 768px) {
  .app {
    background-size: cover;
    padding-top: 0;
    margin-top: 0;
  }
  .page {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}

h1 {
  font-weight: 500;
  font-size: 1.8rem;
  margin-top: 0.4rem;
  margin-bottom: 0.2rem;
}

h2 {
  font-weight: 500;
  font-size: 1.4rem;
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}

p {
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
  line-height: 1.8rem;
}

p,
h1,
h2,
h3,
h4,
h5 {
  color: #333;
}

a {
  text-decoration: none;
  color: #5f27cd;
}

a:hover,
a:active {
  color: #341f97;
}

.text-center {
  text-align: center;
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.5rem;
  }
}

